.hamburger {
  position: relative;
  z-index: 9999;
  height: inherit;
  display: inline-block;
  right: 0;
  width: 2.625rem;
  padding: 0 0.3125rem;
  text-align: center;
  cursor: pointer;
}

.hamburger span {
  position: absolute;
  display: block;
  width: 2rem;
  height: 0.0625rem;
  transition: 0.5s;
  background-color: #fff;
}

.hamburger span:nth-of-type(1) {
  top: 1.75rem;
}

.hamburger span:nth-of-type(2) {
  top: 2.5rem;
}

.hamburger span:nth-of-type(3) {
  top: 3.25rem;
}

.hamburger.is-open span:nth-of-type(1) {
  top: 2.5rem;
  left: 0;
  transform: rotate(37deg);
  width: 2.5625rem;
}

.hamburger.is-open span:nth-of-type(2) {
  opacity: 0;
}

.hamburger.is-open span:nth-of-type(3) {
  top: 2.5rem;
  left: 0;
  transform: rotate(-37deg);
  width: 2.5625rem;
}

