@use 'global' as *;

.top-voice {
	position: relative;
}

.top-voice::before {
		@include mq("md") {
		content: "";
		position: absolute;
		width: rem(301);
		height: rem(138);
		top: -19%;
		left: calc(50% - min(48vw, rem(620)));
		// left: calc(50% - 43vw);
		// left: calc(50% - rem(620));
		background-image: url(../images/common/fish-green.svg);
		// background-size: contain;
		background-size: cover;
		// 要素の領域に画像全体が収まるように拡大縮小される
		transform: scaleX(-1);
		//x軸に反転

	}
}

.top-voice::after {
	@include mq("md") {
		content: "";
		position: absolute;
		width: rem(71);
		height: rem(162);
		bottom: -10.5%;
		right: calc(50% - min(48vw, rem(540)));
		// right: calc(50% - 37.5vw);
		// right: calc(50% - rem(540));
		background-image: url(../images/common/seahorse.svg);
		// background-size: contain;
		background-size: cover;
		// 要素の領域に画像全体が収まるように拡大縮小される
	}
}


.top-voice__cards {
	margin-top: rem(42);

	@include mq("md") {
		margin-top: rem(29);
	}
}

.top-voice__button-wrap {
	margin-top: rem(40);
	text-align: center;

	@include mq("md") {
		margin-top: rem(80);
	}
}